/* ローディング画面 */
#loading {
	width: 100vw;
	height: 100vh;
	text-align: center;
	transition: all 1s;
	background-color: #fbf9f6;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 9999;
}

.spinner {
	animation: sk-scaleout 1s infinite ease-in-out;
	text-align: center;
}

/* ローディングアニメーション */
@keyframes sk-scaleout {
	0% {
		transform: rotate(0);
	}
	100% {
		transform: rotate(360deg);
		transition: 2s;
	}
}
#loading .loaded {
	opacity: 0;
	visibility: hidden;
}

.swiper {
	padding-bottom: 6px;
}

.swiper-slide {
	width: 16.5rem;
	height: 20.6875rem;
	background: #fff;
	box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.16);
	overflow: hidden;
	display: flex;
	flex-direction: column;
	flex-shrink: 0;
}

.swiper__img {
	height: 166px;
	width: 100%;
	object-fit: cover;
}

.swiper__desc {
	padding: 16px 16px 0 16px;
	font-weight: bold;
	line-height: 1.5;
}

.swiper__price {
	margin-top: auto;
	padding: 0 16px 16px 16px;
}